.page {
  page-break-after: always;
  position: relative;
}
.page .form-img {
  width: 100%;
}

.page-details.hanover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10000;
}
.page-details.hanover input[type=date]::-webkit-inner-spin-button, .page-details.hanover input[type=date]::-webkit-calendar-picker-indicator,
.page-details.hanover input[type=year]::-webkit-inner-spin-button,
.page-details.hanover input[type=year]::-webkit-calendar-picker-indicator,
.page-details.hanover input[type=month]::-webkit-inner-spin-button,
.page-details.hanover input[type=month]::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}
.page-details.hanover .form-img {
  width: 100%;
}
.page-details.hanover .answer {
  position: absolute;
  bottom: 0;
}
.page-details.hanover .answer img {
  width: 100%;
  position: absolute;
}
.page-details.hanover .answer input,
.page-details.hanover .answer textarea {
  width: 100%;
  font-size: 10px;
  font-weight: bold;
  color: #141414;
  position: absolute;
  background: transparent;
  overflow: hidden;
}
.page-details.hanover .answer textarea {
  font-size: 10px;
  padding: 0 5px;
}
.page-details.hanover .answer.monospace {
  font-family: monospace;
  letter-spacing: 7px;
}
.page-details.hanover .answer .check-mark {
  position: absolute;
}
.page-details.hanover .answer .check-mark::before, .page-details.hanover .answer .check-mark::after {
  content: "";
  height: 2px;
  width: 15px;
  background: black;
  position: absolute;
  transform: rotate(45deg);
}
.page-details.hanover .answer .check-mark::before {
  transform: rotate(-45deg);
}
.page-details.hanover textarea.form-control {
  border: 0;
  resize: none;
  height: 100%;
}
